html {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%;
  width: 100%;
  position: fixed;
  overflow-y: hidden;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%;
  width: 100%;
  position: fixed;
}

#body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  position: fixed;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-family: "Inconsolata", monospace;
  font-size: 16px;
}

#body-inner {
  margin: 0;
  padding: 1rem;
  height: 100%;
  width: 100%;
  position: fixed;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  overflow-y: scroll;
  overflow-x: hidden;
  overflow-wrap: break-word;
  background-color: white;
  background-size: contain;
  -webkit-scroll-snap-type: proximity;
  -ms-scroll-snap-type: proximity;
  scroll-snap-type: proximity;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
  color: black;
}

/* title */
h1 {
  font-family: "Hanken Grotesk", sans-serif;
  margin: 0;
  font-size: 1.4em;
  letter-spacing: -0.5px;
  line-height: 1.2em;
  font-weight: normal;
}

/* uppercase bolded subheader */
h4 {
  font-family: "Inconsolata", monospace;
  font-size: 0.8em;
  text-transform: uppercase;
}

/* monospace */
h5 {
  font-family: "Inconsolata", monospace;
  font-size: 0.8em;
  font-weight: 400;
}

/* paragraph text */
p {
  font-family: "Hanken Grotesk", sans-serif;
  margin: 1rem 0;
  font-size: 1em;
  /* letter-spacing: -0.1px; */
  line-height: 1.4em;
  font-weight: 300;
}

hr {
  background-color: #e4e4e4;
  border: none;
  height: 1px;
  margin: 4rem 0;
}

.contact-icon {
  width: 2rem;
}

.copyright {
  display: none;
}

.fade {
  opacity: 0;
}

.grid-container {
  display: grid;
  gap: 2rem;
  grid-auto-columns: 1fr;
}

/* tags, keywords on HOME & PROJECT pages */
.grid-item-1 {
  grid-column: 1 / 2;
}

/* main text on PROJECT page (mobile) */
.grid-item-main-text {
  grid-column: 1 / 4;
}

/* project title on HOME (mobile) */
.grid-item-1-5 {
  grid-column: 1 / 4;
}

/* date completed on PROJECT page */
.grid-item-2 {
  grid-column: 2 / 5;
}

/* social links */
.grid-item-4 {
  grid-column: 1 / 4;
}

/* right panel on HOME (mobile) */
.grid-right-panel {
  grid-column: 1 / 4;
  margin-top: 3rem;
}

/* impact on PROJECT page (mobile) */
.grid-item-secondary-text {
  grid-column: 1 / 4;
}

.grid-item-full {
  grid-column: 1 / 4;
}

.grid-item-img {
  grid-column: 1 / 4;
}

.link-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.margin-v {
  margin: 1rem 0;
}

.nav {
  align-self: start;
  display: flex;
  flex-direction: column;
  /* gap: 1rem; */
  /* position: absolute;
  top: auto; */
}

.nav-contact {
  display: flex;
  justify-content: flex-end;
}

.nav-link {
  margin: 0 0.5rem 0 0;
  transition: color 0.2s ease-in-out;
}

.nav-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: -2rem;
}

.pill {
  align-items: center;
  background-color: #f3f3f3;
  border-radius: 16px;
  display: flex;
  line-height: 0px;
  padding: 0px 10px;
  width: fit-content;
  height: 1.5rem;
}

.pill-container {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.proj-img {
  border: 1px solid #e4e4e4;
  border-radius: 12px;
  width: 100%;
  margin: auto;
  height: 30rem;
  background-position: top;
  background-size: cover;
}

#lp-redesign {
  background-image: url("./assets-lp/cover-lp.png");
  background-size: fill;
  background-repeat: no-repeat;
}

#sales-handoff {
  background-image: url("./assets-sales/cover-sales.png");
  background-size: fill;
  background-repeat: no-repeat;
}

#shop-redesign {
  background-image: url("./assets-cart/cover-shop.png");
  background-size: fill;
  background-repeat: no-repeat;
}

.status-icon {
  width: 9px;
  height: 9px;
  background: #43eb25;
  border-radius: 50%;
  animation: pulse 0.7s infinite alternate;
}

/* Mirror-loop pulse: 1 → 0.4 → 1 */
@keyframes pulse {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0.4;
  }
}

.status-wrapper {
  display: flex;
  align-items: center;
  gap: 8px;
}

::-webkit-scrollbar {
  width: 15px;
}

::-webkit-scrollbar-track {
}

::-webkit-scrollbar-thumb {
  /* border-radius: 8px;
  -webkit-box-shadow: none;
  background: rgba(150, 150, 150, 0.5);
  z-index: 3; */
  border: 4px solid rgba(0, 0, 0, 0);
  background-clip: padding-box;
  border-radius: 9999px;
  background-color: #c1c1c1;
  -webkit-box-shadow: none;
}

/* TABLET PORTRAIT */
@media screen and (min-width: 600px) and (orientation: portrait) {
  #body-inner {
    padding: 2rem;
  }

  .grid-container {
    display: grid;
    margin: 5rem 0 0 0;
  }

  /* main text on PROJECT page (tablet) */
  .grid-item-main-text {
    grid-column: 1 / 12;
  }

  .grid-item-secondary-text {
    grid-column: 1 / 12;
  }

  .grid-item-full {
    grid-column: 1 / 12;
  }

  .grid-item-img {
    grid-column: 1 / 12;
  }

  .proj-img {
    height: 40rem;
  }
}

/* TABLET LANDSCAPE */
@media screen and (min-width: 600px) and (orientation: landscape) {
  #body-inner {
    padding: 3rem;
  }

  .contact-icon {
    width: 1.8rem;
  }

  .copyright {
    display: block;
    margin-top: 4rem;
  }

  /* main text on PROJECT page */
  .grid-item-main-text {
    grid-column: 1 / 2;
  }

  /* right panel on HOME */
  .grid-right-panel {
    grid-column: 3 / 12;
    margin: 0;
  }

  /* impact on PROJECT page */
  .grid-item-secondary-text {
    grid-column: 4 / 10;
  }

  /* social links */
  .grid-item-4 {
    grid-column: 4 / 8;
  }

  .grid-item-full {
    grid-column: 1 / 12;
  }

  .grid-item-img {
    grid-column: 4 / 12;
  }

  .nav {
    align-self: start;
    position: sticky;
    top: 0;
  }

  .nav-wrapper {
    display: flex;
  }

  .proj-img {
    height: 32rem;
  }
}

/* DESKTOP OR IPAD PRO LANDSCAPE */
@media screen and (min-width: 1200px) and (orientation: landscape) {
  /* impact on project page */
  .grid-item-secondary-text {
    grid-column: 4 / 8;
  }
}

